import { UserService } from './userService';
import { BrowserModule } from '@angular/platform-browser';
import { HttpClientModule } from '@angular/common/http';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { IonicStorageModule } from '@ionic/storage';

import {StatusBar} from '@ionic-native/status-bar';
import {SplashScreen} from '@ionic-native/splash-screen';
import {Keyboard} from '@ionic-native/keyboard';


import firebaseConfig from './firebase'
import { AngularFireModule } from '@angular/fire';
import { AngularFirestoreModule } from '@angular/fire/firestore';
import { AngularFireStorageModule } from '@angular/fire/storage';
import { AngularFireDatabaseModule } from '@angular/fire/database';
import { AngularFireAuthModule } from '@angular/fire/auth';

import {ActivityService} from "../services/activity-service";
import {TripService} from "../services/trip-service";
import {WeatherProvider} from "../services/weather";

import {MyApp} from "./app.component";

import {LoginPage} from "../pages/login/login";
import {NotificationsPage} from "../pages/notifications/notifications";
import {RegisterPage} from "../pages/register/register";

// import services
// end import services
// end import services

// import pages
// end import pages

@NgModule({
  declarations: [
    MyApp,
    LoginPage,
    NotificationsPage,
    RegisterPage,
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    AngularFireModule.initializeApp(firebaseConfig),
    AngularFireAuthModule,
    AngularFirestoreModule,
    AngularFireStorageModule,
    AngularFireDatabaseModule,
    IonicModule.forRoot(MyApp, {
      scrollPadding: false,
      scrollAssist: true,
      autoFocusAssist: false
    }, 
{
  links: [
    { loadChildren: '../pages/bookings/bookings.module.ngfactory#BookingsPageModuleNgFactory', name: 'BookingsPage', segment: 'bookings', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/categories/categories.module.ngfactory#CategoriesPageModuleNgFactory', name: 'CategoriesPage', segment: 'categories', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/feedback/feedback.module.ngfactory#FeedbackPageModuleNgFactory', name: 'FeedbackPage', segment: 'feedback', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/helpdesk/helpdesk.module.ngfactory#HelpdeskPageModuleNgFactory', name: 'HelpdeskPage', segment: 'helpdesk', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/refer/refer.module.ngfactory#ReferPageModuleNgFactory', name: 'ReferPage', segment: 'refer', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/single-artist/single-artist.module.ngfactory#SingleArtistPageModuleNgFactory', name: 'SingleArtistPage', segment: 'single-artist', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/single-category/single-category.module.ngfactory#SingleCategoryPageModuleNgFactory', name: 'SingleCategoryPage', segment: 'single-category', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/splash/splash.module.ngfactory#SplashPageModuleNgFactory', name: 'SplashPage', segment: 'splash', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/user-profile/user-profile.module.ngfactory#UserProfilePageModuleNgFactory', name: 'UserProfilePage', segment: 'user-profile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/users/users.module.ngfactory#UsersPageModuleNgFactory', name: 'UsersPage', segment: 'users', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot({
      name: 'Baza',
        driverOrder: ['indexeddb', 'sqlite', 'websql']
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    LoginPage,
    NotificationsPage,
    RegisterPage,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Keyboard,
    ActivityService,
    TripService,
    WeatherProvider,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    UserService,
  ]
})

export class AppModule {

  
}
  



